<template>
  <div>
    <!---->
    <breadcrumb
      :breadcrumbItemList="['', '', '']"
    ></breadcrumb>

    <el-card>
      <!---->
      <el-alert title="" type="info" center show-icon> </el-alert>

      <!--steps-->
      <el-steps
        :active="+activeIndex"
        finish-status="success"
        class="step"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <el-form
        :model="addForm"
        :rules="AddRules"
        ref="addForm"
        label-width="100px"
        label-position="top"
      >
        <!--tab-->
        <el-tabs
          tab-position="left"
          v-model="activeIndex"
          @tab-click="handleTabClick"
          :before-leave="beforeLeaveTab"
        >
          <el-tab-pane label="" name="0">
            <!---->
            <el-form-item label="" prop="goods_name">
              <el-col :span="24">
                <el-input v-model="addForm.goods_name"></el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input
                v-model.number="addForm.goods_price"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input
                v-model.number="addForm.goods_weight"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input
                v-model.number="addForm.goods_number"
                type="number"
              ></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                v-model="selectKey"
                :options="allGoodsCategories"
                :props="categoryCasProps"
                @change="handleChange"
                :checkStrictly="true"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <!---->
            <el-form-item
              v-for="item in manyParams"
              :label="item.attr_name"
              :key="item.attr_id"
            >
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox
                  v-for="(checkItem, index) in item.attr_vals"
                  :key="index"
                  :label="checkItem"
                  border
                ></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="2">
            <el-form-item
              v-for="item in onlyParams"
              :label="item.attr_name"
              :key="item.attr_id"
            >
              <el-input v-model="item.attr_vals[0]"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="" name="3">
            <!---->
            <el-upload
              class="upload-demo"
              action="http://timemeetyou.com:8889/api/private/v1/upload"
              :headers="headersObj"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccessUpload"
              list-type="picture-card"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
            <!---->
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="previewImageUrl" alt="" />
            </el-dialog>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <quill-editor
              v-model="addForm.goods_introduce"
              class="quill-height"
            ></quill-editor>
            <el-button type="primary" class="addBtn" @click="addGoods"
              ></el-button
            >
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>

<script>
//
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
//lodash
import _ from "lodash";
//
import Breadcrumb from "components/common/breadcrumb/Breadcrumb";
//
import {
  queryGoodsCategories,
  queryGoodsParams,
  addGoods,
} from "network/addGoods";
export default {
  name: "addGoods",
  components: {
    Breadcrumb,
    quillEditor,
  },
  data() {
    return {
      activeIndex: "0",
      addForm: {
        goods_name: "",
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: "",
        pics: [],
        attrs: [],
      },
      AddRules: {
        goods_name: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_weight: [
          { required: true, message: "", trigger: "blur" },
        ],
        goods_number: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
      allGoodsCategories: [],
      selectKey: [],
      categoryCasProps: {
        label: "cat_name",
        value: "cat_id",
        expandTrigger: "hover",
      },
      manyParams: [],
      onlyParams: [],
      previewImageUrl: "",
      dialogVisible: false,
      headersObj: {
        Authorization: window.sessionStorage.getItem("token"),
      },
    };
  },

  created() {
    this.queryGoodsCategories();
  },
  computed: {
    cateId() {
      return this.selectKey;
    },
  },
  methods: {
    queryGoodsCategories() {
      queryGoodsCategories({}).then((res) => {
        this.allGoodsCategories = res.data;
      });
    },
    handleTabClick() {
      if (this.activeIndex == "1") {
        this.queryGoodsParams("many");
      }
      if (this.activeIndex == "2") {
        this.queryGoodsParams("only");
      }
    },
    queryGoodsParams(sel) {
      queryGoodsParams({
        sel: sel,
        id: this.cateId[2],
      }).then((res) => {
        //attr_vals
        res.data.forEach((x) => {
          // x.attr_vals == ""?
          x.attr_vals = x.attr_vals.length === 0 ? [] : x.attr_vals.split(",");
        });
        sel === "many"
          ? (this.manyParams = res.data)
          : (this.onlyParams = res.data);
      });
    },
    handleChange() {
      this.addForm.goods_cat = this.selectKey;
    },
    beforeLeaveTab(to, from) {
      let result = true;
      //tab
      if (from === "0") {
        this.$refs.addForm.validateField(
          ["goods_name", "goods_price", "goods_weight", "goods_number"],
          (errorMessage) => {
            if (errorMessage) {
              result = false;
            }
          }
        );
      }
      //tab
      //tab
      //tab
      //tab

      return result;
    },
    handleRemove(file, fileList) {
      const delIndex = this.addForm.pics.findIndex((x) => {
        x.pic == file.response.data.tmp_path;
      });
      this.addForm.pics.splice(delIndex, 1);
    },
    handlePreview(file) {
      this.dialogVisible = true;
      this.previewImageUrl = file.response.data.url;
    },
    handleSuccessUpload(response, file, fileList) {
      //,pushaddFormpics
      this.addForm.pics.push({ pic: response.data.tmp_path });
    },
    addGoods() {
      //addFormattrs
      [...this.manyParams.concat(this.onlyParams)].forEach((x) => {
        const attrObj = {
          attr_id: x.attr_id,
          attr_value: x.attr_vals.join(","),
        };
        this.addForm.attrs.push(attrObj);
      });
      //addFormgoods_cats
      const form = _.clone(this.addForm);
      form.goods_cat = form.goods_cat.join(",");
      //
      this.$refs.addForm.validate((valid) => {
        !valid
          ? this.$message.error("")
          : (() => {
              addGoods(form).then((res) => {
                res.meta.status === 201
                  ? (() => {
                      //
                      this.$message.success(res.meta.msg);
                      //
                      this.$router.push("/goods");
                    })()
                  : this.$message.error(res.meta.msg);
              });
            })();
      });
    },
  },
};
</script>

<style scoped>
.el-checkbox {
  margin: 0 10px !important;
}

.addBtn {
  margin-top: 15px;
}
</style>
